#posts_Tbl {
    border-collapse: collapse;
    width: 100%;
  }
  
#posts_Tbl td, #posts_Tbl th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
#posts_Tbl tr:nth-child(even){background-color: #f2f2f2;}
  
#posts_Tbl tr:hover {background-color: #ddd;}
  
#posts_Tbl th {
    text-align: left;
    background-color: #4CAF50;
    color: white;
  }